@font-face {
  font-family: "wixVariableBold";
  src: url("../../Fonts/wixVariable/static/WixMadeforDisplay-Bold.ttf")
    format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "wixVariableMedium";
  src: url("../../Fonts/wixVariable/static/WixMadeforDisplay-Medium.ttf")
    format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

.brendWrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 300px;
  width: 100%;
  flex-wrap: nowrap;
}

.brendTitle {
  display: flex;
  align-items: center;
  font-family: "wixVariableBold";
  font-size: 26px;
  width: 100%;
  height: 70px;
  justify-content: center;
  color: rgba(27, 35, 41, 1);
}

.brendContent {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: flex-start;
  justify-content: space-between;
  align-content: space-between;
  width: 1300px;
  height: 500px;
}

.brendContainerPic {
  border: 1px solid;
}

.brendBtnWrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 20px 0 20px 0;
}

.brendBtn {
  width: 50px;
  height: 35px;
  font-family: "wixVariableBold";
  font-size: 25px;
}
