@font-face {
  font-family: "SofiaSans";
  src: url("../Fonts/sofia_sans/static/SofiaSans-Black.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "SofiaSansMedium";
  src: url("../Fonts/sofia_sans/static/SofiaSans-Medium.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

.headerMenu {
  font-family: "SofiaSans";
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  height: 50px;
  background-color: rgba(27, 35, 41, 1);
  align-items: center;
  text-decoration: none;
  text-transform: uppercase;
  /* flex-wrap: nowrap; */ /* не нужно тут переноситть на другую стоку */
}

.headerMenuAbout {
  display: flex;
  align-items: center;
  height: 50px;
  color: rgba(250, 250, 250, 1);
}

.headerMenuPrice {
  display: flex;
  align-items: center;
  height: 50px;
  color: rgba(250, 250, 250, 1);
}

.headerMenuPay {
  display: flex;
  align-items: center;
  height: 50px;
  color: rgba(250, 250, 250, 1);
}

.headerMenuDelivery {
  display: flex;
  align-items: center;
  height: 50px;
  color: rgba(250, 250, 250, 1);
}

.headerMenuContacts {
  display: flex;
  align-items: center;
  height: 50px;
  color: rgba(250, 250, 250, 1);
}

.headerMenuRegister {
  display: flex;
  align-items: center;
  height: 50px;
  color: rgba(250, 250, 250, 1);
}

.modalMain {
  width: fit-content;
  margin: 0 auto;
  background-color: rgba(250, 250, 250, 1);
}

.headerModalWrapMenuString {
  display: flex;
  align-items: center;
  width: 162px;
  height: 50px;
  color: rgba(250, 250, 250, 1);
  cursor: pointer;
}

.headerMenuRegisterSpace {
  margin-left: 5px;
  margin-right: 5px;
}

.headerMenuTel {
  display: flex;
  align-items: center;
  flex-direction: row;
  height: 50px;
  color: rgba(250, 250, 250, 1);
}

.headerMenuTelPic {
  display: flex;
  height: 20px;
  width: 20px;
  margin-right: 5px;
  background-image: url("../HeaderMenuPic/phone_red.png");
  background-size: 100%;
  background-repeat: no-repeat;
}

.headerMenuTelTel {
  display: flex;
  align-items: center;
  flex-direction: row;
  align-items: center;
  color: rgba(250, 250, 250, 1);
}

.headerMenuLink a {
  cursor: pointer;
  text-decoration: none;
  color: rgba(250, 250, 250, 1);
}

.headerModal {
  display: flex;
  flex-direction: column;
  width: 400px;
  height: 600px;
  border: 2px solid red;
}

.headerModalTitle {
  font-family: "SofiaSans";
  font-size: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.headerModalForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 250px;
  font-family: "SofiaSansMedium";
  font-size: 18px;
}

.paymentFormicLoginP {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
}

.headerModalLogin {
  width: 90%;
}

.headerModalPass {
  width: 90%;
}

.headerModalRegBtn {
  font-family: "SofiaSansMedium";
  font-size: 18px;
  width: 80px;
  text-align: center;
  height: 30px;
  border-radius: 10px;
  color: rgba(250, 0, 0, 1);
  background-color: rgba(169, 169, 169, 0.5);
}

.modalFormBtnLocation {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 93%;
  height: 60px;
}

.headerModalBottomButton {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 60px;
}

.headerModalBtn {
  font-family: "SofiaSansMedium";
  font-size: 18px;
  width: 150px;
  height: 40px;
  border-radius: 10px;
  color: rgba(250, 0, 0, 1);
  background-color: rgba(169, 169, 169, 0.5);
}
