@font-face {
    font-family: "wixVariableBold";
    src: url("../../Fonts/wixVariable/static/WixMadeforDisplay-Bold.ttf") format("truetype"); 
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: "wixVariableMedium";
    src: url("../../Fonts/wixVariable/static/WixMadeforDisplay-Medium.ttf") format("truetype"); 
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

#root {
height: 100%;
}

.contactsWrapper {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    justify-content: center;
}

.contactsCenterWrap {
    display: flex;
    justify-content:space-between;
}

.contactsTitle {
    font-family: "wixVariableBold";
    display: flex;
    justify-content: center;
}

.contactsAddress {
    display: flex;
    flex-direction: column;
    font-family: "wixVariableMedium";
    font-size: 18px;
    width: 30%;
    height: 300px;
    font-size: 20px;
    margin-left: 15px;
}

.contactsMap {
    display: flex;
    flex-direction: column;
    width: 70%;
    height: 500px;
    border: 1px solid;
    margin-bottom: 15px;
}